import { Component, OnInit } from '@angular/core';
import { Customer } from '../entity/customer';
import { CustomerService } from '../services/customer.service'
import { Router } from '@angular/router';
import { Subject, identity } from 'rxjs';
import { Input, EventEmitter} from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  customer: Customer = new Customer();
  submitted = false;
  boolean : Boolean = false; 
  user : Customer = new Customer();
  url : String;

  constructor(private customer_service: CustomerService, private router: Router) { }

  ngOnInit() {
    
  }

  newEmployee(): void {
    this.submitted = false;
    this.customer = new Customer();

  }

  save() {
    this.customer_service.login(this.customer).subscribe(data => { console.log(data); this.boolean = data;     
      if(this.boolean)
      {
        console.log(this.customer.identity);

        this.customer_service.retrieveCustomerDetails(this.customer.identity).subscribe(data => { this.customer = data[0]; console.log(data[0]);  this.customer_service.customerData(data);
        });

        // this.customer_service.retrieveCustomerDetails(this.customer.identity).subscribe(data => { this.customer = data; console.log(data);  this.customer_service.customerData(data);
        // });

        
        this.url = '/' + this.customer.identity + '/items';
        this.router.navigate([this.url]);
      }
      else
      {
        console.log("wrong password or identity");
      }
  }, error => console.log(error));

}

  onSubmit() {
    this.submitted = true;
    this.save();    
  }

  createAccount()
  {
    this.router.navigate(['/signup']);
  }


}



