import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { Routes, RouterModule } from '@angular/router';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// import { NgwWowModule } from 'ngx-wow';

import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';

import { AppComponent } from './app.component';
import { AddItemComponent } from './add-item/add-item.component';
import { DeleteItemComponent } from './delete-item/delete-item.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { BuyCartComponent } from './buy-cart/buy-cart.component';
import { BuyCartStatusComponent } from './buy-cart-status/buy-cart-status.component';
import { CartComponent } from './cart/cart.component';
import { CartStatusComponent } from './cart-status/cart-status.component';
import { InvoiceComponent } from './invoice/invoice.component';
import { ItemComponent } from './item/item.component';
import { ItemCategoryComponent } from './item-category/item-category.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { PaymentComponent } from './payment/payment.component';
import { SearchComponent } from './search/search.component';
import { ShopComponent } from './shop/shop.component';
import { FinalComponent } from './final/final.component';

const routes: Routes = [
 
  { path: 'home', component: HomeComponent },
  { path: 'home/category/:id', component: HomeComponent},
  { path: 'home/search/:keyword', component: HomeComponent},

  { path: 'add-item', component: HeaderComponent}, 
  { path: 'delete-item', component: FooterComponent},
  
  { path: 'signup', component: SignupComponent },
  { path: 'login', component: LoginComponent},
  { path: ':identity/items', component: ShopComponent},
  { path: ':identity/payment', component: PaymentComponent },
  { path: ':identity/category/:id', component: ShopComponent},
  { path: ':identity/search/:keyword', component: ShopComponent},
  { path: ':identity/buy-cart-details', component: BuyCartComponent},
  { path: ':identity/cart-details', component: CartComponent},
  { path: ':identity/invoice', component: InvoiceComponent},
  { path: ':identity/final', component: FinalComponent},
  { path: 'items/:id', component: ItemComponent},

   
  { path: '**' , component: PageNotFoundComponent}
];


@NgModule({
  declarations: [
    AppComponent,
    AddItemComponent,
    DeleteItemComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    LoginComponent,
    SignupComponent,
    BuyCartComponent,
    BuyCartStatusComponent,
    CartComponent,
    CartStatusComponent,
    InvoiceComponent,
    ItemComponent,
    ItemCategoryComponent,
    PageNotFoundComponent,
    PaymentComponent,
    SearchComponent,
    ShopComponent,
    FinalComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    // NgwWowModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(routes)

  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
