import { Component, OnInit } from '@angular/core';
import { BuyCartService } from '../services/buy-cart.service'
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-buy-cart-status',
  templateUrl: './buy-cart-status.component.html',
  styleUrls: ['./buy-cart-status.component.css']
})
export class BuyCartStatusComponent implements OnInit {

  totalPrice: number = 0;
  totalQuantity: number = 0;
  identity: String;

  constructor(private _buycartService: BuyCartService, private _activatedRoute : ActivatedRoute) { }

  ngOnInit() {
    this.updateBuyCartStatus();
    this.identity = this._activatedRoute.snapshot.paramMap.get("identity");
  }
  
  updateBuyCartStatus() {
    //subscribe to the events
    this._buycartService.totalPrice.subscribe(
      data => this.totalPrice = data
    ) 
    
    this._buycartService.totalQuantity.subscribe(
      data => this.totalQuantity = data
    )
  }

}
