import { Component, OnInit } from '@angular/core';
import {ItemService} from '../services/item.service';
import {ItemCategory} from '../entity/item-category';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-item-category',
  templateUrl: './item-category.component.html',
  styleUrls: ['./item-category.component.css']
})
export class ItemCategoryComponent implements OnInit {

  itemCategories: ItemCategory[];
  identity: String;

  constructor(private _activatedRoute : ActivatedRoute,private _itemService: ItemService) { }

  ngOnInit() {
    this.listItemCategories();
    this.identity = this._activatedRoute.snapshot.paramMap.get("identity");
  }

  listItemCategories(){
    this._itemService.getItemCategories().subscribe(
      itemCategories => this.itemCategories = itemCategories
    );
  }

}
